body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #f4f4f4;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #222;
  color: white;
  padding: 1rem 2rem;
}

.navbar .brand {
  font-weight: bold;
}

.navbar ul {
  display: flex;
  gap: 1rem;
  list-style: none;
}

.navbar ul li a {
  color: white;
  text-decoration: none;
}

.auth-buttons .login-btn,
.auth-buttons .signup-btn {
  margin-left: 1rem;
  padding: 5px 10px;
  background: #f5c518;
  color: #222;
  border-radius: 5px;
  text-decoration: none;
}

.card {
  background: white;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
  width: 150px;
}
