.faq-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: calc(100vh - 70px);
  background: linear-gradient(135deg, #f5f7fa, #c3cfe2);
  padding: 3rem 1rem;
}

.faq-wrapper h1 {
  text-align: center;
  color: #222;
  margin-bottom: 3rem;
  font-size: 2.5rem;
  animation: fadeIn 1s ease-in-out;
}

.faq-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  max-width: 1200px;
}

.faq-card {
  background: rgba(245, 197, 24, 0.1); /* glass effect */
  padding: 2rem;
  border-radius: 16px;
  width: 300px;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s, background 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.faq-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
  background: rgba(245, 197, 24, 0.2);
}

.faq-card h3 {
  color: #222;
  margin-bottom: 1rem;
  font-weight: 600;
}

.faq-card p {
  color: #555;
  font-size: 0.95rem;
  line-height: 1.5;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.4s ease, opacity 0.4s ease;
  opacity: 0;
}

.faq-card.active p {
  max-height: 200px;
  opacity: 1;
}

/* FadeIn animation */
@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(-20px); }
  100% { opacity: 1; transform: translateY(0); }
}
