.page-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 70px); /* adjust for navbar */
  background: linear-gradient(135deg, #f5f7fa, #c3cfe2);
}

.page-container {
  text-align: center;
  padding: 3rem 4rem;
  background: rgba(255,255,255,0.85);
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
  animation: fadeIn 1s ease-in-out;
}

.page-container h1 {
  font-size: 2.5rem;
  color: #222;
  margin-bottom: 1rem;
}

.page-container p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  color: #555;
}

.buttons button {
  margin: 0.5rem 1rem;
  padding: 0.8rem 2rem;
  border-radius: 12px;
  border: none;
  background: #f5c518;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

.buttons button:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0,0,0,0.2);
}

@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(-20px); }
  100% { opacity: 1; transform: translateY(0); }
}
