.about-container {
  max-width: 900px;
  margin: 3rem auto;
  padding: 1rem;
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.about-title {
  font-size: 2.5rem;
  color: #f5c518;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 8px rgba(0,0,0,0.5);
}

.about-mission {
  font-size: 1.2rem;
  margin-bottom: 3rem;
  color: #fff;
  line-height: 1.6rem;
  background: linear-gradient(90deg, #6a11cb, #2575fc);
  display: inline-block;
  padding: 1rem 2rem;
  border-radius: 12px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.4);
  transition: transform 0.4s ease, background 0.5s ease;
}

.about-mission:hover {
  transform: translateY(-5px) scale(1.02);
  background: linear-gradient(90deg, #ff416c, #ff4b2b);
}

.team-title {
  font-size: 2rem;
  color: #f5c518;
  margin-bottom: 2rem;
  text-shadow: 1px 1px 6px rgba(0,0,0,0.5);
}

.team-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.team-card {
  background: linear-gradient(135deg, #00c6ff, #0072ff);
  color: #fff;
  border-radius: 16px;
  padding: 1.5rem 2rem;
  width: 200px;
  text-align: center;
  cursor: pointer;
  transition: transform 0.4s ease, box-shadow 0.4s ease, background 0.5s ease;
  box-shadow: 0 10px 20px rgba(0,0,0,0.4);
}

.team-card:hover {
  transform: translateY(-8px) scale(1.05);
  box-shadow: 0 15px 25px rgba(0,0,0,0.5);
  background: linear-gradient(135deg, #ff416c, #ff4b2b);
}

.team-card .avatar {
  background: #f5c518;
  color: #000;
  font-weight: bold;
  font-size: 1.5rem;
  width: 60px;
  height: 60px;
  margin: 0 auto 1rem auto;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}
